import React, { useState } from 'react'
import { Document, Page, pdfjs} from 'react-pdf';
import styled from 'styled-components'

import GlobalWrapper from 'components/global-wrapper'
import media from 'utils/media-queries'
import Link from 'gatsby-link'

import Header from 'components/article/header'
import CoverImage from 'components/article/coverimage'
import ContentWrapper from 'components/article/contentwrapper'
import Footer from 'components/footer'
import FullSizeImage from 'components/article/fullsizeimage'

import portfolioPDF from 'pages/ekta_2021_cv.pdf'

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const Section = styled.section`
  text-align: center;
  margin: 0 0;
  ${media.sm`
    margin: 0;
  `}
`

const PDFSection = styled.section`
  margin: 8px 0;
  ${media.xl`
    margin: 8px 0;
  `}
  ${media.lg`
    margin: 8px 0;
  `}
  ${media.md`
    margin: 8px 0;
  `}
  ${media.sm`
    margin: 8px 0;
  `}
`

const Content = styled.div`
  canvas {
    width: 100% !important;
    height: auto !important;
  }
  max-width: 1000px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  p + h3 {
    margin-top: 42px;
  }
  padding: 0 0 0 0;
  ${media.lg`
    padding-right: 0;
  `}
  ${media.xs`
  max-width: 700px;
  padding: 0 0 0 0;
  `}
`

const Img = styled.img`
  max-width: calc(100% - 96px);
  max-height: 300px;
  margin: 8px;
  display: inline-block;
  ${media.xl`
    max-height: 240px;
  `}
  ${media.lg`
    max-height: 200px;
  `}
  ${media.md`
    max-height: 120px;
    margin: 4px;
  `}
  ${media.sm`
    max-height: 80px;
    margin: 2px;
  `}
`


const CV = () => {
  const [numPages, setNumPages] = useState(null);
  return (
    <GlobalWrapper>
      <Header title="Vita" />
      <Section>
        <Content>
          <p> <Link to="https://github.com/ektarakholiya/ektarakholiya.github.io/raw/master/static/ekta_2021_cv-a27526641f22507c2cc22277030a5d10.pdf">Download PDF</Link> </p>
          <Document 
            file={portfolioPDF} 
            onLoadSuccess={({ numPages })=>setNumPages(numPages)}>
            {Array.apply(null, Array(numPages))
    .map((x, i)=>i+1)
    .map(page => <Page pageNumber={page} renderMode="svg" scale={1.72}/>)}
          </Document>
        </Content>
      </Section>
      <Footer />
    </GlobalWrapper>
  )
}

export default CV
